:root {
  --third-color: #FF6B6B; /* New vibrant accent color */
}

.about {
  padding: 40px 0;
}

.about-container {
  max-width: 900px;
  margin: 0 auto;
  padding: 0 40px;
}

.about h1 {
  font-size: 3rem;
  margin: 0 0 40px;
  color: var(--white-color);
  text-align: center;
  position: relative;
  display: inline-block;
}

.about h1:after {
  content: '';
  position: absolute;
  bottom: -10px;
  left: 0;
  width: 100%;
  height: 3px;
  background: linear-gradient(to right, var(--accent-color), var(--third-color));
}

.about-section {
  margin-bottom: 60px;
}

.about-section:last-child {
  margin-bottom: 0;
}

.about-section h2 {
  font-size: 1.8rem;
  margin: 0 0 16px;
  color: var(--white-color);
}

.about-section p {
  margin: 0 0 16px;
  line-height: 1.6;
  color: var(--text-color);
}

.education-item {
  margin-bottom: 24px;
}

.education-item h3 {
  margin: 0 0 8px;
  font-size: 1.4rem;
  color: var(--white-color);
}

.education-details {
  font-weight: 500;
  margin: 0 0 4px !important;
}

.education-date {
  opacity: 0.7;
  margin: 0 0 16px !important;
  font-size: 0.9rem;
}

.about-section ul {
  margin: 0;
  padding-left: 24px;
}

.about-section li {
  margin-bottom: 8px;
  color: var(--text-color);
}

.skills-container {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 24px;
}

.skills-category h3 {
  font-size: 1.2rem;
  margin: 0 0 12px;
  color: var(--white-color);
}

/* Experience Styles */
.experience-item {
  margin-bottom: 32px;
  padding-bottom: 32px;
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
}

.experience-item:last-child {
  margin-bottom: 0;
  padding-bottom: 0;
  border-bottom: none;
}

.experience-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 8px;
}

.experience-header h3 {
  margin: 0;
  font-size: 1.4rem;
  color: var(--white-color);
}

.experience-date {
  opacity: 0.7;
  font-size: 0.9rem;
}

.experience-company {
  font-weight: 500;
  color: var(--accent-color);
  margin: 0 0 12px;
}

.experience-item ul {
  margin: 0;
  padding-left: 24px;
}

.experience-item li {
  margin-bottom: 8px;
  color: var(--text-color);
  line-height: 1.5;
}

/* Activity Styles */
.activity-item {
  margin-bottom: 24px;
}

.activity-item:last-child {
  margin-bottom: 0;
}

.activity-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 8px;
}

.activity-header h3 {
  margin: 0;
  font-size: 1.2rem;
  color: var(--white-color);
}

.activity-date {
  opacity: 0.7;
  font-size: 0.9rem;
}

.activity-role {
  font-weight: 500;
  color: var(--accent-color);
  margin: 0 0 8px;
}

.activity-description {
  margin: 0;
  line-height: 1.6;
  color: var(--text-color);
}

/* Timeline Styles */
.timeline {
  position: relative;
  padding-left: 40px;
  margin-top: 30px;
}

.timeline:before {
  content: '';
  position: absolute;
  left: 10px;
  top: 0;
  bottom: 0;
  width: 1px;
  background: linear-gradient(to bottom, 
    rgba(255, 255, 255, 0.1),
    rgba(255, 255, 255, 0.2),
    rgba(255, 255, 255, 0.1)
  );
  opacity: 0.5;
}

.timeline-item {
  position: relative;
  margin-bottom: 40px;
}

.timeline-item:last-child {
  margin-bottom: 0;
}

.timeline-dot {
  position: absolute;
  left: -40px;
  top: 5px;
  width: 12px;
  height: 12px;
  border-radius: 50%;
  background: var(--accent-color);
  border: 2px solid rgba(255, 255, 255, 0.2);
  box-shadow: 0 0 8px rgba(255, 255, 255, 0.1);
}

.timeline-date {
  font-size: 0.9rem;
  color: var(--third-color);
  margin-bottom: 8px;
  font-weight: 500;
}

.timeline-content h3 {
  margin: 0 0 8px;
  font-size: 1.4rem;
  color: var(--white-color);
}

.timeline-location {
  font-style: italic;
  opacity: 0.8;
  margin-bottom: 12px;
}

.timeline-content p {
  margin: 0 0 12px;
  line-height: 1.6;
}

.timeline-description {
  color: var(--text-color);
  line-height: 1.6;
  margin: 0;
  font-size: 0.95rem;
}

.timeline-content ul {
  margin: 0;
  padding-left: 20px;
}

.timeline-content li {
  margin-bottom: 8px;
  line-height: 1.5;
}

/* Skills Grid Styles */
.skills-grid {
  display: flex;
  flex-direction: column;
  gap: 24px;
  margin-top: 20px;
}

.skill-category {
  background-color: rgba(255, 255, 255, 0.03);
  border-radius: 8px;
  padding: 20px;
}

.skill-tag {
  display: inline-block;
  background: linear-gradient(to right, var(--accent-color), var(--third-color));
  color: var(--white-color);
  padding: 4px 12px;
  border-radius: 20px;
  font-size: 0.9rem;
  font-weight: 500;
  margin-bottom: 16px;
}

.skill-items {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
}

.skill-items span {
  background-color: rgba(255, 255, 255, 0.05);
  color: var(--text-color);
  padding: 6px 12px;
  border-radius: 4px;
  font-size: 0.9rem;
  transition: all 0.2s;
}

.skill-items span:hover {
  background-color: rgba(255, 107, 107, 0.1);
  transform: translateY(-2px);
  color: var(--white-color);
}

/* Contact Info Styles */
.contact-info {
  display: flex;
  flex-direction: column;
  gap: 16px;
}

.contact-link {
  color: var(--text-color);
  text-decoration: none;
  transition: color 0.2s;
  display: flex;
  align-items: center;
}

.contact-link:hover {
  color: var(--third-color);
}

.contact-icon {
  margin-right: 10px;
  font-style: normal;
}

.contact-info p {
  display: flex;
  align-items: center;
  margin: 0;
}

/* Function Block Styles */
.function-block {
  position: relative;
  margin-bottom: 60px;
  padding: 0 0 0 20px;
}

.function-name {
  font-family: 'Consolas', 'Monaco', monospace;
  font-size: 1.6rem;
  color: var(--text-color);
  margin: 0 0 20px;
  display: flex;
  align-items: center;
}

.function-content {
  padding-left: 20px;
}

.function-end {
  font-family: 'Consolas', 'Monaco', monospace;
  font-size: 1.6rem;
  color: var(--text-color);
  margin-top: 20px;
  padding-left: 0;
}

/* Timeline Header with Logo */
.timeline-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 8px;
}

.company-logo {
  width: 80px;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-left: 15px;
  border-radius: 6px;
  padding: 5px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
  transition: all 0.3s ease;
}

.company-logo:hover {
  transform: scale(1.05);
  box-shadow: 0 4px 12px rgba(255, 107, 107, 0.2);
}

.company-logo img {
  max-width: 100%;
  max-height: 100%;
  object-fit: contain;
}

/* Adjust timeline content for logo layout */
.timeline-content h3 {
  margin: 0;
  font-size: 1.4rem;
  color: var(--white-color);
}

/* Contact Links Styling */
.contact-links {
  display: flex;
  justify-content: center;
  gap: 2rem;
  margin: 1.5rem 0;
}

.contact-link {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 1rem;
  border-radius: 8px;
  background: rgba(255, 255, 255, 0.05);
  transition: all 0.3s ease;
  width: 150px;
  text-decoration: none;
}

.contact-link:hover {
  background: rgba(255, 255, 255, 0.1);
  transform: translateY(-5px);
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
}

.contact-link i {
  font-size: 2rem;
  margin-bottom: 0.5rem;
  color: var(--third-color);
}

.contact-link span {
  font-size: 0.9rem;
  color: var(--text-color);
}

/* Responsive styling */
@media (max-width: 768px) {
  .about-container {
    padding: 0 20px;
  }
  
  .about h1 {
    font-size: 2.2rem;
  }
  
  .about-section h2 {
    font-size: 1.5rem;
  }
  
  .timeline {
    padding-left: 30px;
  }
  
  .timeline-dot {
    left: -30px;
    width: 12px;
    height: 12px;
  }
  
  .timeline-content h3 {
    font-size: 1.2rem;
  }
  
  .function-block {
    padding-left: 15px;
  }
  
  .function-name {
    font-size: 1.4rem;
  }
  
  .function-content {
    padding-left: 10px;
  }
  
  .function-end {
    font-size: 1.4rem;
  }
  
  .timeline-header {
    flex-direction: column;
    align-items: flex-start;
  }
  
  .company-logo {
    margin-left: 0;
    margin-top: 10px;
    width: 70px;
    height: 35px;
  }
  
  .contact-links {
    flex-direction: column;
    align-items: center;
    gap: 1rem;
  }
  
  .contact-link {
    width: 100%;
    max-width: 300px;
  }
} 