.footer {
  padding: 40px 0;
  margin-top: 40px;
}

.footer-container {
  max-width: 1280px;
  margin: 0 auto;
  padding: 0 16px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.footer p {
  color: var(--text-color);
  opacity: 0.7;
  margin: 0;
}

.footer-links {
  display: flex;
  gap: 24px;
}

.footer-links a {
  color: var(--text-color);
  opacity: 0.7;
  text-decoration: none;
  font-size: 14px;
  transition: opacity 0.3s ease, color 0.3s ease;
}

.footer-links a:hover {
  opacity: 1;
  color: var(--accent-color);
}

@media (max-width: 768px) {
  .footer-container {
    flex-direction: column;
    align-items: center;
    text-align: center;
  }
  
  .footer-links {
    margin-top: 16px;
  }
} 